.recipe {
    min-height: 100vh;
    display: flex;
    position: relative;
    @include sm {
        flex-flow: column; }
    &__close {
        background-color: transparent;
        height: 80px;
        width: 80px;
        border-radius: 50%;
        background-color: #f0890f;
        @include vcenter;
        z-index: 10;
        @include sm {
            display: none; }
        .icon {
            font-size: 30px;
            color: $white; } }
    &__image {
        flex: 0 0 50%;
        width: 50%;
        @include cover;
        @include sm {
            width: 100%;
            padding-bottom: 300px; } }
    &__content {
        flex: 0 0 50%;
        width: 50%;
        padding-top: 136px;
        background-color: #f7ede0;
        @include sm {
            width: 100%; } }
    &__title {
        font-family: $budidaya;
        font-size: 48px;
        color: #0d70b7;
        font-weight: 700;
        margin-left: 34%;
        margin-bottom: 68px;
        letter-spacing: 0.04em; }
    &__ingredients,
    &__actions {
        display: flex; }
    &__picture {
        text-align: center;
        flex: 0 0 34%; }
    &__icon {
        display: block;
        font-size: 0;
        .icon {
            color: #ce2617; } }
    &__subtitle {
        font-family: $montserrat;
        font-size: 22px;
        font-weight: 600;
        color: #ce2617;
        text-transform: uppercase;
        margin-top: 18px; }
    &__ingredients {
        margin-top: 54px;
        @include sm {
            flex-flow: column; }
        .icon {
            font-size: 80px; } }
    &__ingredients-list {
        position: relative;
        flex: 0 0 57%;
        &:before,
        &:after {
            position: absolute;
            left: 0;
            content: '';
            height: 2px;
            background-color: #0d70b7;
            width: 100%; }
        &:before {
            top: -54px;
            @include sm {
                display: none; } }
        &:after {
            bottom: 0; } }
    &__ingredient {
        display: block;
        position: relative;
        padding-left: 24px;
        margin-bottom: 56px;
        @include sm {
            display: inline-block; }
        &:last-child {
            margin-bottom: 90px; }
        &:after {
            content: '';
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            width: 2px;
            background-color: #ff9d11; } }
    &__measure {
        font-family: $montserrat;
        font-size: 30px;
        font-weight: 600;
        color: #767474;
        line-height: 1;
        margin-bottom: 14px; }
    &__number {
        font-size: 36px; }
    &__product {
        font-family: $montserrat;
        font-size: 24px;
        line-height: 1;
        color: #2e7dbb;
        font-weight: 600; }
    &__actions {
        margin-top: 74px;
        .icon {
            font-size: 92px; } }
    &__actions-list {
        list-style-type: decimal;
        flex: 0 0 57%;
        padding-left: 28px;
        padding-top: 12px; }
    &__action {
        font-family: $arial;
        font-size: 24px;
        line-height: 1.5;
        color: #6b6b6b;
        padding-left: 20px;
        margin-bottom: 1.5em; } }
