.ingredient-name_dark-green { color: #32592e; }

.ingredient-name_purple { color: #8792c7; }

.ingredient-circle { position: relative; width: 12px; height: 12px; border-radius: 50%; margin-left: 14px; }

.ingredient-circle:after { content: ""; position: absolute; background-color: #8b8b8b; width: 1px; height: 14px; top: -32px; left: 50%; transform: translateX(-50%); }

.ingredient-circle_brown { background-color: #583b1c; box-shadow: 0 0 10px 4px rgba(88, 59, 28, 0.8); }

.ingredient-circle_gray { background-color: #6e6358; box-shadow: 0 0 10px 4px rgba(110, 99, 88, 0.8); }

.ingredient-circle_light-brown { background-color: #9a7b55; box-shadow: 0 0 10px 4px rgba(154, 123, 85, 0.8); }

.ingredient-circle_white-brown { background-color: #ba9b6f; box-shadow: 0 0 10px 4px rgba(186, 155, 111, 0.8); }

.ingredient-circle_green { background-color: #5da457; box-shadow: 0 0 10px 4px rgba(93, 164, 87, 0.8); }

.ingredient-circle_dark-green { background-color: #32592e; box-shadow: 0 0 10px 4px rgba(50, 89, 46, 0.8); }

.ingredient-circle_purple { background-color: #8792c7; box-shadow: 0 0 10px 4px rgba(135, 146, 199, 0.8); }

.recipe { min-height: 100vh; display: flex; position: relative; }

@media only screen and (max-width: 767px) { .recipe { flex-flow: column; } }

.recipe__close { background-color: transparent; height: 80px; width: 80px; border-radius: 50%; background-color: #f0890f; position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); z-index: 10; }

@media only screen and (max-width: 767px) { .recipe__close { display: none; } }

.recipe__close .icon { font-size: 30px; color: #fff; }

.recipe__image { flex: 0 0 50%; width: 50%; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; }

@media only screen and (max-width: 767px) { .recipe__image { width: 100%; padding-bottom: 300px; } }

.recipe__content { flex: 0 0 50%; width: 50%; padding-top: 136px; background-color: #f7ede0; }

@media only screen and (max-width: 767px) { .recipe__content { width: 100%; } }

.recipe__title { font-family: "Budidaya", Helvetica, sans-serif; font-size: 48px; color: #0d70b7; font-weight: 700; margin-left: 34%; margin-bottom: 68px; letter-spacing: 0.04em; }

.recipe__ingredients, .recipe__actions { display: flex; }

.recipe__picture { text-align: center; flex: 0 0 34%; }

.recipe__icon { display: block; font-size: 0; }

.recipe__icon .icon { color: #ce2617; }

.recipe__subtitle { font-family: "Montserrat", Helvetica, sans-serif; font-size: 22px; font-weight: 600; color: #ce2617; text-transform: uppercase; margin-top: 18px; }

.recipe__ingredients { margin-top: 54px; }

@media only screen and (max-width: 767px) { .recipe__ingredients { flex-flow: column; } }

.recipe__ingredients .icon { font-size: 80px; }

.recipe__ingredients-list { position: relative; flex: 0 0 57%; }

.recipe__ingredients-list:before, .recipe__ingredients-list:after { position: absolute; left: 0; content: ''; height: 2px; background-color: #0d70b7; width: 100%; }

.recipe__ingredients-list:before { top: -54px; }

@media only screen and (max-width: 767px) { .recipe__ingredients-list:before { display: none; } }

.recipe__ingredients-list:after { bottom: 0; }

.recipe__ingredient { display: block; position: relative; padding-left: 24px; margin-bottom: 56px; }

@media only screen and (max-width: 767px) { .recipe__ingredient { display: inline-block; } }

.recipe__ingredient:last-child { margin-bottom: 90px; }

.recipe__ingredient:after { content: ''; position: absolute; top: 0; bottom: 0; left: 0; width: 2px; background-color: #ff9d11; }

.recipe__measure { font-family: "Montserrat", Helvetica, sans-serif; font-size: 30px; font-weight: 600; color: #767474; line-height: 1; margin-bottom: 14px; }

.recipe__number { font-size: 36px; }

.recipe__product { font-family: "Montserrat", Helvetica, sans-serif; font-size: 24px; line-height: 1; color: #2e7dbb; font-weight: 600; }

.recipe__actions { margin-top: 74px; }

.recipe__actions .icon { font-size: 92px; }

.recipe__actions-list { list-style-type: decimal; flex: 0 0 57%; padding-left: 28px; padding-top: 12px; }

.recipe__action { font-family: "Arial", sans-serif; font-size: 24px; line-height: 1.5; color: #6b6b6b; padding-left: 20px; margin-bottom: 1.5em; }

.info { padding: 63px 0 92px; text-align: center; }

.info__title { color: #0d70b7; margin-bottom: 25px; }

.info__description { margin: 0 auto; max-width: 955px; color: #a2a2a2; font-size: 24px; }

.info__row { margin: 0 -32px; display: flex; flex-wrap: wrap; }

.info__row:not(:first-child) { margin-top: 35px; }

.info__col { max-width: 33.3%; flex: 0 0 33.3%; padding: 0 32px; }

.info__article { text-align: left; height: 100%; color: #fff; display: flex; flex-flow: row wrap; align-content: space-between; border: 1px solid #CFCFCF; background-color: #0d70b7; }

.info__img { height: 500px; background-position: 50% 50%; background-size: cover; display: block; }

.info__wrap { padding: 27px 27px 16px 27px; }

.info__date { margin-bottom: 10px; font-size: 12px; }

.info__title-sm { line-height: 1.1; font-family: "Montserrat", Helvetica, sans-serif; font-size: 24px; font-weight: 600; color: #fff; transition: border-color .3s; border-bottom: 1px solid transparent; }

.no-touch .info__title-sm:hover { border-color: currentColor; }

.info__text { margin-top: 15px; font-size: 18px; line-height: 1.3; letter-spacing: -0.01em; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.info__text p:not(:first-child) { margin-top: 15px; }

.info__author { min-width: 100%; text-align: right; font-weight: 700; font-size: 14px; padding: 0 27px 16px; }

.info__mail { margin-top: 25px; text-align: right; height: 0; }

.info__mail .mail-submit { margin: -77px -27px 0 0; }

.info__all { margin-top: 31px; text-align: right; }

.info__read { position: relative; margin-right: -5px; padding-right: 45px; color: #e9082f; font-size: 20px; vertical-align: top; display: inline-block; }

.info__read .icon { position: absolute; top: 7px; right: 0; transform: rotate(-90deg); color: #4993C9; font-size: 19px; }

.mail-submit { text-align: right; padding: 7px; width: 406px; font-family: "Montserrat", Helvetica, sans-serif; font-size: 20px; font-weight: 600; color: #fff; font-size: 0; background-color: #ff9d11; vertical-align: top; display: inline-block; }

.mail-submit:after { content: " "; display: table; clear: both; }

.mail-submit__icon { float: left; width: 108px; min-height: 60px; line-height: 60px; text-align: center; border-right: 1px solid #fff; vertical-align: top; display: inline-block; }

.mail-submit__icon .icon { font-size: 37px; vertical-align: middle; display: inline-block; }

.mail-submit__text { float: right; padding-right: 23px; max-width: calc(100% - 117px); min-height: 60px; line-height: 60px; font-size: 20px; }

.footer { padding: 100px 0 67px; color: #0d70b7; font-size: 20px; background-color: #ffe1b7; }

.footer__copy { float: left; }

.footer__soc { float: right; padding-right: 30px; }

.soc { font-size: 0; line-height: 50px; }

.soc__item { vertical-align: middle; display: inline-block; }

.soc__item:not(:first-child) { margin-left: 85px; }

.soc__link { font-size: 34px; line-height: 1; color: #0d70b7; display: block; }

.soc__link .icon-facebook { font-size: 42px; }

.soc__link .icon-instagram { font-size: 37px; }
